import React, { Component } from 'react';
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom'
import {connect} from 'react-redux';
import Register from './register/register.js';
import CardList from './home/CardList.js';
import TopBar from './TopBar';
import Profile from './profile/profile';
import './App.css';
import LoginArea from './login/loginArea';
import { newGame, login, changePage } from '../actions';
const mapStateToProps = state => {
    return {
        actualPage: state.pageController.actualPage,
        Games: state.catalogController.Games
    }
}
const mapDispatchToProps = dispatch => {
    return {
        onSearch: data => dispatch(newGame(data)),
        login: data => dispatch(login(data)),
        changePage: page => dispatch(changePage())
    }
}
class App extends Component {
    showPage = (actualPage) => {
        switch (actualPage) {
            case 'HOME':
                return <Redirect to='/' />
            case 'LOGIN':
                return <Redirect to='/login' />
            case 'PROFILE':
                return <Redirect to='/profile' />
            default:
                return <Redirect to='/' />
        }
    }
    componentDidMount() {
        fetch("https://fliperio.herokuapp.com/page/1", 
        {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(response => response.json())
        .then(data => {
            const {onSearch} = this.props;
            onSearch(data); 
        })
        
        fetch('https://fliperio.herokuapp.com/auth/google/check', {
            method: 'GET',
            headers: {
                'Content-Type' : 'application/json',
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Credentials':true
            },
            credentials: 'include'
        })
        .then(response => response.json())
        .then(data => {
            if(data === 'LOGIN_SUCCESS') {    
                const {login, changePage} = this.props;
                login('LOGIN_SUCCESS');
            }
        })
    }
    render() {
        const {actualPage} = this.props;
        return (
            <BrowserRouter>
                <TopBar/>
                {this.showPage(actualPage)}
                <Switch>
                    <Route path="/" exact={true} component={() => <CardList Games={this.props.Games}/>} />
                    <Route path="/login" component={LoginArea}/>
                    <Route path="/register" component={Register}/>
                    <Route path="/profile" component={Profile}/>
                </Switch>
            </BrowserRouter>
        );
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(App);
